<template>
  <div class="page-wrapper page-m3u8 flex-h">
    <div class="m3u8-content">
      <el-input
        v-model="url"
        clearable
        style="max-width: 600px"
        placeholder=".m3u8"
        class="input-with-select"
        @clear="handleClear"
      >
        <template #prepend>
          <el-button type="primary">{{ downloadLoading ? ` ${downloadProgress}%` : '' }}</el-button>
        </template>
        <template #append>
          <el-button type="primary" @click="handleStart">{{ downloadLoading ? '' : '' }}</el-button>
        </template>
      </el-input>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { ElNotification } from 'element-plus'

const url = ref('')
const downloadLoading = ref(false)
const downloadProgress = ref(0)

const handleStart = () => {
  if (downloadLoading.value) {
    downloadLoading.value = false
    downloadProgress.value = 0
    return
  }
  downloadProgress.value = 0
  downloadLoading.value = true
  const m3u8Url = url.value
  const video = document.createElement('video')
  const hls = new Hls()

  //  .m3u8 
  hls.loadSource(m3u8Url)
  hls.attachMedia(video)

  // 
  hls.on(Hls.Events.ERROR, (event, data) => {
    console.error('HLS Error:', data)
    if (data.fatal) {
      switch (data.type) {
        case Hls.ErrorTypes.NETWORK_ERROR:
          console.error('...')
          hls.startLoad()
          break
        case Hls.ErrorTypes.MEDIA_ERROR:
          console.error('...')
          hls.recoverMediaError()
          break
        default:
          console.error('')
          hls.destroy()
          downloadLoading.value = false
          ElNotification.error({
            title: 'Error',
            message: ''
          })
          break
      }
    }
  })

  let totalFragments = 0
  let loadedFragments = 0

  //  LEVEL_LOADED 
  hls.on(Hls.Events.LEVEL_LOADED, (event, data) => {
    if (data.details && data.details.fragments) {
      totalFragments = data.details.fragments.length
      console.log(`: ${totalFragments}`)
    }
  });

  //  MANIFEST_PARSED 
  hls.on(Hls.Events.MANIFEST_PARSED, async () => {
    console.log('...')

    try {
      // 
      await video.play().catch(error => {
        console.error(':', error)
      })
      console.log('...')

      //  MediaRecorder
      const stream = video.captureStream()
      const mediaRecorder = new MediaRecorder(stream, {
        mimeType: 'video/webm',
        videoBitsPerSecond: 1000000 //  1 Mbps
      })

      const chunks = []
      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunks.push(event.data)
        }
      }

      mediaRecorder.onstop = () => {
        // 
        const blob = new Blob(chunks, { type: 'video/webm' })
        const url = URL.createObjectURL(blob)

        // 
        const a = document.createElement('a')
        a.href = url
        a.download = 'video.webm'
        a.click()
        URL.revokeObjectURL(url)

        downloadLoading.value = false
        downloadProgress.value = 100
      }

      // 
      mediaRecorder.start()
      console.log('')

      // 
      const updateProgress = (loadProgress, currentTime, duration) => {
        let recordProgress = 0
        if (!isNaN(duration) && duration !== Infinity) {
          recordProgress = (currentTime / duration) * 100
        }
        const overallProgress = (loadProgress + recordProgress) / 2; // 
        downloadProgress.value = overallProgress.toFixed(2)
      }

      // 
      hls.on(Hls.Events.FRAG_LOADED, (event, data) => {
        loadedFragments++
        const loadProgress = (loadedFragments / totalFragments) * 100
        updateProgress(loadProgress, video.currentTime, video.duration)
      })

      // 
      video.addEventListener('timeupdate', () => {
        const loadProgress = (loadedFragments / totalFragments) * 100
        updateProgress(loadProgress, video.currentTime, video.duration)
      })

      // 
      video.addEventListener('ended', () => {
        mediaRecorder.stop()
        console.log('')
      });
    } catch (error) {
      console.error(':', error)
    }
  })
}

const handleClear = () => {
  url.value = ''
}

</script>

<style lang="less" scoped>
.page-m3u8 {
  .m3u8-content {
    width: 600px;
    padding-top: 300px;
  }
}
</style>